.player {
  width: 780px;
  min-height: 360px;
  padding: 20px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

@media screen and (max-width: 780px) {
  .player {
    width: 100%;
    justify-content: center;
    padding: 0;
    min-height: unset;
  }
}
